body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.app-container {
  display: flex;
  height: 100vh;
  padding-left: 1px;
}

.modal {
  background: #a2a29d8a;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
}

.modal .loader {
  position: absolute;
  width: 200px;
  top: 30%;
  left: calc(50% - 100px);
}

.modal .loader .message {
  width: 100%;
  text-align: center;
}

.modal .loader .spinner {
  margin: auto;
  width: 75px;
  height: 75px;
  border: 5px solid #bccf0030;
  border-radius: 50%;
  border-top-color: #638e07;
  -webkit-animation: spin 1s cubic-bezier(.71,.42,.27,.6) infinite;
          animation: spin 1s cubic-bezier(.71,.42,.27,.6) infinite;
}

@-webkit-keyframes spin {
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.point-input {
  font-size: 1.5em;
  height: 2em;
  box-sizing: border-box;
  width: 100%;
}

.point-input::-webkit-input-placeholder {
  color: #bababa;
}

.point-input::-moz-placeholder {
  color: #bababa;
}

.point-input::-ms-input-placeholder {
  color: #bababa;
}

.point-input::placeholder {
  color: #bababa;
}

.point-input.error {
  border-color: #fa9191;
  outline-color: #f00;
  outline: #f00;
}

.point-title-container {
  border: 1px solid  #efefef;
  list-style-type: none;
  background: #fff;
  cursor: -webkit-grab;
  cursor: grab;
  display: flex;
  padding: 0 10px;
  justify-content: space-between;
  align-items: center;
  font-size: 1.5em;
  height: 2em;
}

.point-title {
  max-width: 80%;
  overflow: hidden;
  text-overflow: ellipsis;

}

.point-delete-button {
  cursor: pointer;
  fill: #515151
}

.point-delete-button:hover {
  cursor: pointer;
  fill: #ff6347;
}

.points-list {
  padding: 0;
  margin: 0;
  width: 100%;
  box-sizing: border-box;
}

.route-editor {
  flex: 1 0 25%;
  min-width: 19em;
}

